export const useCallsFilter = (type: string) => {
  switch (type) {
    case 'in_out': {
      return {
        defaultValue: ' ',
        items: [
          { value: '1', name: '' },
          { value: '0', name: '' },
        ],
      }
    }
    case 'from_type[]': {
      return {
        defaultValue: ' ',
        items: [
          { name: '', value: 'clients' },
          { name: ' ', value: 'new_clients' },
          { name: '', value: 'workers' },
          { name: '', value: 'app' },
        ],
      }
    }
    case 'sources[]': {
      return {
        defaultValue: ' ',
        items: [
          { name: ' ', value: 'from_site' },
          { name: 'yandex ', value: 'yandex' },
          { name: 'google ', value: 'google' },
          { name: ' ', value: 'empty' },
        ],
      }
    }
    case 'errors[]': {
      return {
        defaultValue: ' ',
        items: [
          { name: ' ', value: 'noerrors' },
          { name: '  ', value: 'noscript' },
        ],
      }
    }
    default: {
      return { defaultValue: 'None', items: [] }
    }
  }
}
